import { Storage } from "aws-amplify";
import axios from "axios";
import { log } from "utils/log";

export async function requestS3ByUrl(url, timeout = 5000) {
  log.debug("url from requestS3ByUrl", url);
  const awsUrl = await Storage.get(url);
  const request = axios.create({ baseURL: awsUrl, timeout: timeout });
  const data = (await request.get(awsUrl)).data;

  log.debug("requestS3ByUrl", data);
  return data;
}
