import { Storage } from "aws-amplify";
import { useDispatch } from "react-redux";
import { constants } from "./constant";
import { log } from "../components/utils/log";
import { requestS3ByUrl } from "./utils";

export default class HomeService {
  dispatch;
  constructor() {
    this.dispatch = useDispatch();
  }

  async getCategories() {
    log.debug("getCategories request data");
    return await requestS3ByUrl("data/quotes/category_with_icon_info.json");
  }

  async getDailyQuote() {
    log.debug("getDailyQuote");
    return await requestS3ByUrl(constants.DAILY_QUOTE_URL);
  }
}
